import * as AColorPicker from 'a-color-picker';
export default {
    name: 'custom-color-picker',
    inject:['componentWraper'],
    props: {
        value: {
            type: [String, Number],
            default: '#ff0000'
        }
    },
    data() {
        return {
            show: false
        }
    },
    computed: {

    },
    methods: {
        onBlur(){
            this.show = false;
            if(this.componentWraper){
				this.componentWraper.$emit('onCancelEdit');
			}
        }
    },
    watch: {
        show: function (val, old) {
            if (val) {
                setTimeout(() => {
                    this.colorPicker = AColorPicker
                        .createPicker(this.$refs['color-picker-panel'],{
                            color:this.value,
                            showAlpha:true,
                            showRGB:true,
                            showHSL:false
                        });
                    this.colorPicker.on('change', (picker, color) => {
                        this.$emit('input',color);
                    });
                })
            } else {
                this.colorPicker && (this.colorPicker.destroy());
            }
        }
    },
    mounted() {
        if (this.show === true) {
            this.colorPicker = AColorPicker
                .createPicker(this.$refs['color-picker-panel']);
            this.colorPicker.setColor(this.value);
            this.colorPicker.on('change', (picker, color) => {
                console.log('color-picker-change', picker, color);
            });
        }
    },
    destroy() {

    }
}
