export default {
	name:'dynamic-component',
	mounted(){
		
	},
	render(createElement,context){
		let _this = this;
		return createElement(`${this.$attrs.type}`,{
			props:this.$attrs.config,
			on:{
				input:function(val){
					_this.$emit('input', val);
					_this.$attrs.config.value = val;
				}
			}
		});
	}
}