import componentList from '@/config/component-list';
import { cloneElemNode } from '@/services/cloneElem2body';

const IconsCategory = componentList.reduce((p, n) => {
	if (!p[n.category]) {
		p[n.category] = [];
		p[n.category].push(n);
	} else {
		p[n.category].push(n);
	}
	return p;
}, {});

export default {
	name: 'component-list',
	data() {
		return {
			...IconsCategory
		}
	},
	methods: {
		onItemMousedown: function (event, type) {
			cloneElemNode(event.target, event, type);
		}
	},
	mounted() {

	}
};
