export default {
    name:'custom-switch',
    model:{
        
    },
    props: {
        disabled: {
            type: Boolean,
            default: false
        },
        checked: {
            type: Boolean,
            default: false
        },
        checkedText: {
            type: String,
            default: ''
        },
        uncheckedText: {
            type: String,
            default: ''
        }
    },
    data() {
        return {

        }
    },
    methods: {
        onClick() {
            if (this.disabled) {
                return;
            }
            this.checked = !this.checked;
            this.$emit('change', this.checked);
        }
    }
}
