let selectedComponents = [];
let startPosition = {
	left: 0,
	top: 0
};
/**
 * @desc 设置拖拽开始位置
 * @param {Event} e 事件
 * @returns {undefined} 无返回值
 */
export function setStartPostion(e) {
	startPosition.left = e.pageX;
	startPosition.top = e.pageY;
};
/**
 * @desc 获取拖拽开始位置
 * @returns {undefined} 无返回值
 */
export function getStartPosition() {
	return startPosition;
};
/**
 * @desc 添加或移除选中的元素
 * @param {ComponentWraper} 组件
 * @returns {undefined} 无返回值
 */
export function toggleSelectedComponent(component) {
	if (!selectedComponents.find(element => element === component)) {
		selectedComponents.push(component);
	} else {
		for (let i = 0; i < selectedComponents.length; i++) {
			if (selectedComponents[i] === component) {
				selectedComponents.splice(i, 1);
				break;
			}
		}
	}
}
/**
 * @desc 删除销毁组件
 * param {ComponentWraper} 组件
 * @returns {undefined} 无返回值
 */
export function deleteSelectedComponent(component) {
	if (selectedComponents.find(element => element === component)) {
		for (let i = 0; i < selectedComponents.length; i++) {
			if (selectedComponents[i] === component) {
				selectedComponents.splice(i, 1);
				break;
			}
		}
	}
};
/**
 * @desc 清空选中的元素
 * @returns {undefined} 无返回值
 */
export function emptySelectedComponents() {
	selectedComponents = [];
}
/**
 * @desc 移动选中的元素
 * @param {Number} translateX X轴方向平移值
 * @param {Number} translateY Y轴方向平移值
 * @returns {undefined} 无返回值
 */
export function translateSelectedComponents(translateX, translateY, ) {
	selectedComponents.forEach(component => {
		let { left, top } = component;
		component.$el.style.left = (left + translateX) + 'px';
		component.$el.style.top = (top + translateY) + 'px';
	});
	try {
		if (selectedComponents.length > 0) {
			let referElementX = document.getElementById('selected-components-refer-x');
			let referElementY = document.getElementById('selected-components-refer-y');
			let referElementPosition = document.getElementById('selected-components-refer-position');
			let group = document.getElementById('selected-components-group');
			let left = Math.min.apply(null, selectedComponents.map(component => parseInt(component.$el.style.left))) - (selectedComponents.length > 1 ? 1 : 0);
			let top = Math.min.apply(null, selectedComponents.map(component => parseInt(component.$el.style.top))) - (selectedComponents.length > 1 ? 1 : 0);
			referElementX.style.left = left + 'px';
			referElementY.style.top = top + 'px';
			referElementPosition.style.left = left + 'px';
			referElementPosition.style.top = top + 'px';
			referElementPosition.querySelector('span:first-child').innerHTML = left;
			referElementPosition.querySelector('span:last-child').innerHTML = top;
			if (selectedComponents.length > 1) {
				let group = document.getElementById('selected-components-group');
				group.style.left = left + 'px';
				group.style.top = top + 'px';
			}
		}
	} catch (err) {
		console.error(err);
	}

}
/**
 * @desc 结束平移选中的元素并重置偏移值
 * @returns {undefined} 无返回值
 */
export function endTranslateSelectedComponents() {
	selectedComponents.forEach(component => {
		component.$el.style.transform = `translate(${0}px,${0}px)`;
	});
}
/**
 * @desc 改变选中元素的尺寸大小
 * @param {Number} 缩放比
 * @returns {undefined} 无返回值
 */
export function resizeSelectedComponents(rate) {
	let minLeft = Math.min.apply(null, selectedComponents.map(component => component.left));
	let maxLeft = Math.max.apply(null, selectedComponents.map(component => component.left));
	let minTop = Math.min.apply(null, selectedComponents.map(component => component.top));
	let maxTop = Math.max.apply(null, selectedComponents.map(component => component.top));
	let componentsRelative = selectedComponents.map(component => {
		return {
			left: component.left - minLeft,
			top: component.top - minTop
		}
	});
	selectedComponents.forEach((component, idx) => {
		let { width, height } = component;
		component.$el.style.width = Math.floor((width * rate)) + 'px';
		component.$el.style.height = Math.floor((height * rate)) + 'px';
		component.$el.style.left = Math.floor((minLeft + (componentsRelative[idx].left * rate))) + 'px';
		component.$el.style.top = Math.floor((minTop + (componentsRelative[idx].top * rate))) + 'px';
		requestAnimationFrame(() => {
			component.$emit('onComponentResizing', {
				width: component.$el.style.width.replace(/\D/g, ''),
				height: component.$el.style.height.replace(/\D/g, '')
			});
		});
	});
}
