
import CollapsePanel from '../CollapsePanel/CollapsePanel.component.vue';
export default {
    name: 'custom-collapse',
    components: {
        CollapsePanel
    },
    props: {
        defaultActiveKey: {
            type: [String,Number],
            default: ''
        },
        activeKey: {
            type: [String,Number],
            default: ''
        },
        accordion: {
            type: Boolean,
            default: false
        },
        styles:{
            type:Object,
            default:function(){
                return {
                    border: '1px solid #d9d9d9'
                }
            }
        }
    },
    data() {
        return {

        }
    },
    provide:function(){
        return {
            collapse:this
        }
    }
}


