export default {
    name: 'custom-collapse-panel',
    props: {
        title: {
            type: String,
            default: ''
        },
        value: {
            type: [String,Number],
            default: ''
        },
        styles:{
            type:Object,
            default: function(){
                return {
                    backgroundColor:'#fafafa',
                    color:'#000000'
                }
            }
        }
    },
    data() {
        return {
            
        }
    },
    computed:{
        show:function(){
            return this.collapse.activeKey === this.value;
        }
    },
    inject:['collapse'],
    methods: {
        onPanelClick() {
            this.collapse.activeKey = this.value;
        }
    },
    watch:{

    }
}
