/**
 * @desc 矩形碰撞检测
 * @param {Object} rc1 矩形
 * @param {Object} rc2 矩形
 * @returns {Boolean} 是否碰撞
 */
export function isOverlap(rc1, rc2) {
    if (rc1.left + rc1.width > rc2.left &&
        rc2.left + rc2.width > rc1.left &&
        rc1.top + rc1.height > rc2.top &&
        rc2.top + rc2.height > rc1.top
    ) {
        return true;
    }
    else {
        return false;
    }
}
/**
 * @desc 获取目标元素到body左上角的距离
 * @param {Element} elem DOM元素
 * @returns {Object} left,top距离
 */
export function getOffset2body(elem) {
    var offset = {
        left: 0,
        top: 0
    };
    offset.left += elem.offsetLeft;
    offset.top += elem.offsetTop;
    var parent = elem;
    while (parent.offsetParent) {
        parent = parent.offsetParent;
        offset.left += parent.offsetLeft;
        offset.top += parent.offsetTop;
    }
    return offset;
}
/**
 * @desc 判断值是否是数字类型
 * @param {Any} number 需要判断的值
 * @return {Boolean} 是否是数字类型
 */
export function isNumberic(number) {
    if (typeof number === 'number') {
        return true;
    } else if (number !== null && !isNaN(number)) {
        return true;
    }
    return false;
}
/**
 * @desc 判断值是否是颜色类型的
 * @param {Any} color 需要判断的值
 * @return {Boolean} 是否是颜色类型
 */
export function isColorLike(color) {
    return color ? true : false; 
}
/**
 * @desc 组件(*.config.js)内的配置项转为组件使用的属性值
 * @param {Object} config 配置项
 * @return {Object} 组件使用的属性值
 */
export function config2props(config) {
    let props = {};
    config.forEach(prop => {
        if (prop.type === 'group') {
            props[prop.props] = prop.group.reduce((p, n) => {
                if (!p[n.props]) {
                    p[n.props] = n.value;
                }
                return p;
            }, {});
        } else {
            if (!props[prop.props]) {
                props[prop.props] = prop.value;
            }
        }
    });
    return props;
}
/**
 * @desc 全屏
 * @param {Element} ele 申请全屏的元素
 */
export function requestFullScreen(ele) {
    if (ele === undefined) {
        ele = document.body;
    }
    if (ele.requestFullscreen) {
        ele.requestFullscreen();
    } else if (ele.mozRequestFullScreen) {
        ele.mozRequestFullScreen();
    } else if (ele.webkitRequestFullscreen) {
        ele.webkitRequestFullscreen();
    } else if (ele.msRequestFullscreen) {
        ele.msRequestFullscreen();
    }
}
/**
 * @desc 退出全屏
 */
export function exitFullscreen() {
    if (document.exitFullScreen) {
        document.exitFullScreen();
    } else if (document.mozCancelFullScreen) {
        document.mozCancelFullScreen();
    } else if (document.webkitExitFullscreen) {
        document.webkitExitFullscreen();
    } else if (document.msExitFullscreen) {
        document.msExitFullscreen();
    }
}

/**
 * 图片转blob
 * @param {ImageElement} image
 * @return {Promise} 
 */
export function image2blob(image) {
    let resolvePointer = null;
    let rejectPointer = null;
    let promise = new Promise((resolve, reject) => {
        resolvePointer = resolve;
        rejectPointer = reject;
    });
    if (!(image instanceof Element)) {
        rejectPointer('传入参数不是元素');
    }
    let { width, height } = image.getBoundingClientRect();
    let offScreenCanvas = document.createElement('canvas');
    let context = offScreenCanvas.getContext('2d');
    offScreenCanvas.width = width;
    offScreenCanvas.height = height;
    try {
        context.drawImage(image, 0, 0, width, height);
    } catch (err) {
        rejectPointer('传入的参数不是图片元素');
    }
    let readTimer = setTimeout(() => {
        rejectPointer('读取图片数据超时');
    }, 1000)
    offScreenCanvas.toBlob((blob) => {
        clearTimeout(readTimer);
        resolvePointer(blob);
    });
    return promise;
}
/**
 * blob转图片base64
 * @param {Blob} blob
 * @return {base64String} 
 */
export function blob2image(blob) {
    let resolvePointer = null;
    let rejectPointer = null;
    let promise = new Promise((resolve, reject) => {
        resolvePointer = resolve;
        rejectPointer = reject;
    });
    if (!(blob instanceof Blob)) {
        rejectPointer('传入参数不是blob对象');
    }
    else if (!/image\/\w*/.test(blob.type)) {
        rejectPointer('blob对象的类型不是图片');
    }
    let reader = new FileReader();
    try {
        reader.readAsDataURL(blob);
    } catch (err) {
        rejectPointer('读取blob对象失败');
    }
    reader.onload = function (event) {
        let { result } = event.target;
        resolvePointer(result);
    };
    return promise
}