export default {
    name:'custom-select',
    inject:['componentWraper'],
    props:{
        value:{
            type:String,
            default:''
        },
        options:{
            type:Array,
            default:function(){
                return []
            }
        }
    },
    data(){
        return {
            visible:false,
            searchkey:''
        }
    }
}